/* ==========================================================================
   WooCommerce Onsale and Out of Stock responsive styles - begin
   ========================================================================== */

@include ipad-portrait {

    .eltdf-woo-single-page {

	    &.eltdf-woo-single-thumb-on-left-side {

		    .eltdf-single-product-content {

			    .eltdf-onsale,
			    .eltdf-out-of-stock,
			    .eltdf-new-product {
				    left: 10px;
			    }
		    }
	    }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock responsive styles - end
   ========================================================================== */